@import 'global.scss';

:host {
  /**
   * @prop --accordion-default-font-family: The default font family.
   * @prop --accordion-heading-padding: The padding of the heading.
   * @prop --accordion-heading-font-family: The font family of the heading.
   * @prop --accordion-heading-font-size: The font size of the heading.
   * @prop --accordion-heading-font-weight: The font weight of the heading.
   * @prop --accordion-content-line-height: The line height of the heading.
   * @prop --accordion-content-padding: The padding of the content.
   * @prop --accordion-content-font-family: The font family of the content.
   * @prop --accordion-content-font-size: The font size of the content.
   * @prop --accordion-content-line-height: The line height of the content.
   * @prop --accordion-content-font-weight: The font weight of the content.
   * @prop --accordion-divider-background-color: The background color of divider.
   * @prop --accordion-divider-height: The height of the divider.
   * @prop --accordion-icon-color: The color of the heading icon.
   * @prop --accordion-icon-open-transform: The transform of the icon on open state.
   * @prop --accordion-animation-duration: The duration of the transition of the state change of the accordion.
   *
   * @prop display: The display mode of the host element.
   */
  --accordion-default-font-family: #{$rwc-font-family-default};
  
  --accordion-heading-padding: #{$rwc-spacing--xs} 0;
  --accordion-heading-font-family: var(--accordion-default-font-family);
  --accordion-heading-font-size: #{$rwc-font-size-heading-sm};
  --accordion-heading-font-weight: 500;
  --accordion-content-line-height: #{$rwc-line-height-xs};
  
  --accordion-content-padding: #{$rwc-spacing--xs} 0 #{$rwc-spacing--md} 0;
  --accordion-content-font-family: var(--accordion-default-font-family);
  --accordion-content-font-size: #{$rwc-font-size-body-md};
  --accordion-content-font-weight: 400;
  --accordion-content-line-height: #{$rwc-line-height-xs};
  
  --accordion-divider-background-color: #{$rwc-color-divider-neutral};
  --accordion-divider-height: 1px;
  
  --accordion-icon-color: #{$rwc-color-grey-400};
  --accordion-icon-open-transform: rotate(-180deg);
  
  --accordion-animation-duration: 200ms;
  
  display: block;
}

.heading {
  cursor: pointer;
  user-select: none;
  font-family: var(--accordion-heading-font-family);
  font-size: var(--accordion-heading-font-size);
  font-weight: var(--accordion-heading-font-weight);
  line-height: var(--accordion-content-line-height);
  padding: var(--accordion-heading-padding);
  
  &.with-icon {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.left,
.right {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.left {
  flex-grow: 1;
}

.right {
  flex-shrink: 0;
}

.icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  fill: var(--accordion-icon-color);
  transition: transform var(--accordion-animation-duration) ease-in-out;
}

.content-wrapper {
  height: 0;
  overflow: hidden;
  transition: height var(--accordion-animation-duration) ease-in-out;
}

.content {
  font-family: var(--accordion-default-font-family);
  font-size: var(--accordion-content-font-size);
  font-weight: var(--accordion-content-font-weight);
  padding: var(--accordion-content-padding);
}

.divider {
  background-color: var(--accordion-divider-background-color);
  height: var(--accordion-divider-height);
}

.open {
  .content-wrapper {
    height: auto;
  }
  
  .icon {
    &.rotate {
      transform: var(--accordion-icon-open-transform);
    }
  }
}
