import { Component, Element, Fragment, FunctionalComponent, h, Host, Method, Prop } from '@stencil/core';
import ExpandMore from '@material-design-icons/svg/round/expand_more.svg';

@Component({
  tag: 'rwc-accordion',
  styleUrl: './accordion.scss',
  shadow: true
})
export class RwcAccordion {
  /**
   * The timeout callback for the end of the animation.
   */
  private animationCallback: NodeJS.Timer;

  /**
   * Reference to the content wrapper element.
   */
  private contentWrapperElement: HTMLElement;

  /**
   * Reference to the content element.
   */
  private contentElement: HTMLElement;

  /**
   * Reference to the host element of the component.
   */
  @Element() host: HTMLRwcAccordionElement;

  /**
   * The open state of the accordion.
   */
  @Prop({ reflect: true, mutable: true }) open: boolean = false;

  /**
   * Enables rendering of the divider.
   */
  @Prop() renderDivider: boolean = true;

  /**
   * Enables rendering of the heading icon.
   */
  @Prop() renderIcon: boolean = true;
  
  /**
   * The duration of the animation from the style of the host element.
   */
  private get animationDuration(): number {
    return Number.parseInt(getComputedStyle(this.host).getPropertyValue('--accordion-animation-duration'));
  }

  /**
   * The heading element click event handler.
   */
  private onHeadingClick(): void {
    this.setState(!this.open);
  }

  /**
   * Sets the open state of the accordion to the passed value
   */
  @Method()
  public async setState(value: boolean): Promise<void> {
    if (this.animationCallback !== null) {
      clearTimeout(this.animationCallback);
    }
    
    this.open = value;
    
    if (value) {
      this.contentWrapperElement.style.height = `${ this.contentElement.offsetHeight }px`;
    } else {
      this.contentWrapperElement.style.height = `${ this.contentElement.offsetHeight }px`;
      await new Promise(requestAnimationFrame);
      this.contentWrapperElement.style.height = `0px`;
    }
    
    this.animationCallback = setTimeout(() => {
      this.contentWrapperElement.style.height = '';
      this.animationCallback = null;
    }, this.animationDuration);
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host class={ {
        open: this.open,
      } }>
        <div class={ {
          container: true,
          open: this.open
        } }>
          <div class={ {
            heading: true,
            'with-icon': this.renderIcon
          } }
            onClick={ () => this.onHeadingClick() }
          >
            <div class="left">
              <slot name="heading" />
            </div>
            { this.renderHeadingIconContainer() }
          </div>
          <div
            ref={ (element: HTMLElement) => this.contentWrapperElement = element }
            class="content-wrapper">
            <div
              ref={ (element: HTMLElement) => this.contentElement = element }
              class="content">
              <slot />
            </div>
          </div>
          { this.renderDividerElement() }
        </div>
      </Host>
    );
  }

  /**
   * Renders the divider element if divider property enabled.
   */
  private renderDividerElement(): FunctionalComponent {
    if (this.renderDivider) {
      return <div class="divider"></div>
    }
    return <Fragment></Fragment>;
  }

  /**
   * Renders the heading icon container
   */
  private renderHeadingIconContainer(): FunctionalComponent {
    if (this.renderIcon) {
      return (
        <div class="right">
          <slot name="heading-icon">
            { this.renderDefaultIcon() }
          </slot>
        </div>
      )
    }
    return <Fragment></Fragment>
  }

  /**
   * Renders the default right icon for the accordion heading.
   */
  private renderDefaultIcon(): FunctionalComponent {
    return <div class="icon rotate" innerHTML={ ExpandMore }></div>;
  }
}
